  async getUser(userEmail) {
    let myroute = servername + '/user'
    console.log('getUser: about to make the call to api. email is: ', userEmail)
    const params = {
     userEmail: userEmail}
    let res = await axios.get(myroute, { params });
    return res.data;
  },

